body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  background-color: #d3d3d3;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  display: flex;
}

.logo {
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

nav ul li {
  margin-left: 10px;
}

.theme-switcher {
  cursor: pointer;
  margin-left: 20px;
}

.menu-overlay {
  width: 100%;
  height: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, .5);
  transition: height .3s;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.menu-overlay.open {
  height: 100vh;
}

.menu {
  width: 200px;
  height: 100vh;
  box-sizing: border-box;
  z-index: 1000;
  background-color: #fff;
  padding: 20px;
  transition: transform .3s;
  position: relative;
  transform: translateX(-100%);
}

.menu.open {
  transform: translateX(0);
}

